<template>
  <div class="daypay">
    <div class="bg">
      <el-image
        style="width: 100%; height: 100%"
        :src="gold.getAssetsFile(gamelist.bg)"
        fit="cover"
        @click="setting.getShow"
      />
    </div>
    <div class="gamebox">
      <div class="topday">
        <div class="boxline">
          <el-popover
            placement="top"
            trigger="hover"
            v-for="(item,index) in gamelist.award.slice(0,5)"
            :key='index'
          >
            <template #reference>
              <!-- 顶部宝箱 -->
              <div class="goldbox" @click="gold.tackaward(index,gold.isactive(item.issure,item.istacke))">
                <div :class="['box'+item.count,{'active':gold.isactive(item.issure,item.istacke)}]"></div>
                <div :class="{'istacke':gold.isactive(item.issure,item.istacke)}"></div>
              </div>
            </template>
            <template #default>
              <div
                class="demo-rich-conent"
                style="display: flex; gap: 16px;"
              >
                <el-image
                  style="margin-bottom: 8px;width: 60px;"
                  :src="gold.getAssetsFile(item.img)"
                  :fit="'cover'">
                </el-image>
                <div>
                  <p
                    class="demo-rich-content__mention"
                    style="margin: 0; font-size: 14px; color: var(--el-color-info)"
                  >
                    {{item.name}}
                  </p>
                </div>
              </div>
            </template>
          </el-popover>
        </div>
        <el-progress :percentage="gamelist.percentage" :color="gamelist.customColors">
          <span>{{daycounts}}</span>
        </el-progress>
      </div>
      <div class="gamelist auto-scroll">
        <div class="gameitem" v-for="(item, index) in fixlist" :key="index">
          <div class="gamename">{{ item.name }}</div>
          <div class="flsb mt5">
            <div class="gamecount">
              <div class="coupon">
                <img src="../assets/assets_coupon.png" alt="" />
              </div>x{{ item.count }}
            </div>
            <div class="gamebtn" v-if="!item.isdone" @click="gamelist.receive(item)">
              领 取
            </div>
          </div>
        </div>
      </div>
      <div class="footweek">
          <div class="weekbg">
              <div class="weekcount">本周活跃度 <span>{{weekcount}}</span></div>
              <el-popover
                placement="top-start"
                trigger="hover"
                :content="gamelist.award[5].name"
              >
                <template #reference>
                  <div class="awardbox">
                      <div class="imgbox"  @click="gold.tackaward(5,gold.isactive(gamelist.award[5].issure,gamelist.award[5].istacke))">
                          <img src="../assets/500.png" alt="" :class="{'active':gold.isactive(gamelist.award[5].issure,gamelist.award[5].istacke)}">
                          <div :class="{'istacke':gold.isactive(gamelist.award[5].issure,gamelist.award[5].istacke)}"></div>
                      </div>{{gamelist.award[5].count}}
                  </div>
                </template>
              </el-popover>
              <el-popover
                placement="top-start"
                trigger="hover"
                :content="gamelist.award[6].name"
              >
                <template #reference>
                  <div class="awardbox">
                      <div class="imgbox"  @click="gold.tackaward(6,gold.isactive(gamelist.award[6].issure,gamelist.award[6].istacke))">
                          <img src="../assets/1000.png" alt="" :class="{'active':gold.isactive(gamelist.award[6].issure,gamelist.award[6].istacke)}">
                          <div :class="{'istacke':gold.isactive(gamelist.award[6].issure,gamelist.award[6].istacke)}"></div>
                      </div>{{gamelist.award[6].count}}
                  </div>
                </template>
              </el-popover>
          </div>
      </div>
    </div>
  </div>
  <transition 
    enter-active-class="animate__animated animate__zoomIn"
    leave-active-class="animate__animated animate__zoomOut">
    <div class="surebg" v-if="gold.sureshow">
      <div class="surebox">
        <div class="toptip">恭喜获得</div>
        <div class="suremain">
          <div class="awardb" v-if="gold.count<30">
            <div class="awardimg">
              {{gold.count}}
            </div>
            <div class="awardt">
              点券
            </div>
          </div>
          <div class="awardb" v-else>
            <div class="awardimg noimg">
              <img :src="gold.getAssetsFile(gold.img)" alt="">
            </div>
            <div class="awardt">
              {{gold.awardTip}}
            </div>
          </div>
        </div>
        <div class="surecolose" @click="gold.surecolose">
          确定
        </div>
      </div>
    </div>
  </transition>
  <div class="fixup" @click="setting.dialogFormVisible = true">DIYGame</div>
  <div class="fixup his" @click="setting.gotohis">Histry</div>
  <div class="fixup goback" @click="setting.goback">GoBack</div>
  <div class="fixup pickbg" @click="setting.pickbg">PickBG</div>
  <el-dialog v-model="setting.dialogFormVisible" title="我的人生,我做主" custom-class='diybox'>
    <!-- 奖励表格定制 -->
    <div class="table minw">
      <div class="table-row header">
        <div class="content">分数</div>
        <div class="content">30</div>
        <div class="content">60</div>
        <div class="content">90</div>
        <div class="content">120</div>
        <div class="content">150</div>
        <div class="content">500</div>
        <div class="content">1000</div>
      </div>
      <!-- 上午 -->
      <div class="table-row">
        <div class="label">
          奖励名称
        </div>
        <div
          class="content"
          v-for="(item, index) in gamelist.award"
          :key="index"
        >
          <input type="text" class="input" v-model="item.name" />
        </div>
      </div>
    </div>
    <hr class="m10">
    <!-- 任务表格定制 -->
    <div class="table">
      <div class="table-row header">
        <div class="content">分数</div>
        <div class="content">任务名称</div>
      </div>
      <!-- 上午 -->
      <div class="table-row" 
          v-for="(item, index) in gamelist.list"
          :key="index">
        <div class="content">
          <input type="text" class="input" v-model="item.count" />
        </div>
        <div class="content">
          <input type="text" class="input" v-model="item.name" />
        </div>
      </div>
    </div>
    <div class="flexcon">
      <el-button round @click="setting.contrw">删除任务</el-button>
        <el-button round type="primary" @click="setting.addrw"
          >增加任务</el-button
        >
    </div>
    <template #footer>
      <span class="dialog-footer">
        <el-button @click="setting.dialogFormVisible = false" round>取消</el-button>
        <el-button type="primary" @click="setting.suregameon" round>确认</el-button>
      </span>
    </template>
  </el-dialog>
  <el-dialog v-model="setting.checkBgBox" title="选择背景" custom-class='diybox imgdiy'>
    <div class="imgList">
      <div class="imgboss" v-for="(item, index) in setting.srcList" :key="index">
        <el-image
          class="imgboxdiy"
          :src="item"
          :zoom-rate="1.2"
          :preview-src-list="[item]"
          :initial-index="4"
          fit="cover"
        />
        <div class="iconbox">
          <span class="el-image-viewer__btn el-image-viewer__select" :class="{'bggreen':gamelist.index==index}" @click="setting.pick(index)">
            <svg class="icon" style="width: 1em;height: 1em;vertical-align: middle;fill: currentColor;overflow: hidden;" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="2286"><path d="M384 690l452-452 60 60-512 512-238-238 60-60z" p-id="2287"></path></svg>
          </span>
        </div>
      </div>
      
    </div>
  </el-dialog>
  <el-image-viewer
    v-if="setting.showViewer"
    @close="setting.closeViewer"
    @switch="setting.switchViewer"
    :url-list="setting.srcList"
    :initial-index="gamelist.index"
  >
    <span class="el-image-viewer__btn el-image-viewer__select" @click="setting.pick()">
      <svg class="icon" style="width: 1em;height: 1em;vertical-align: middle;fill: currentColor;overflow: hidden;" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="2286"><path d="M384 690l452-452 60 60-512 512-238-238 60-60z" p-id="2287"></path></svg>
    </span>
  </el-image-viewer>
</template>

<script setup>
    import { reactive,computed,watch } from 'vue' // props emit
    import { useRouter } from 'vue-router'
    //跳转到game
    //#region
    const router = useRouter(); // router 对应了 vue2 中的 $router 对象
//#endregion
    let gamelist = reactive({
      //背景图片
      bg: "bg/0.jpg",
      index: 0,
      count: "",
      //每日得分
      daycount: "",
      //每周得分
      weekcount: "",
      //今天年月日
      today: {
          day:'',
          todaylist:[]
      },
      //游戏人生
      list: [
        {
          count: "5",
          name: "一节多邻国",
          isdone: false,
        },
        {
          count: "25",
          name: "一堆多邻国",
          isdone: false,
        },
        {
          count: "5",
          name: "一篇掘金(面试)",
          isdone: false,
        },
        {
          count: "25",
          name: "一堆掘金(面试)",
          isdone: false,
        },
        {
          count: "5",
          name: "早起,不迟到",
          isdone: false,
        },
        {
          count: "5",
          name: "早睡(11点睡)",
          isdone: false,
        },
        {
          count: "10",
          name: "做家务",
          isdone: false,
        },
        {
          count: "10",
          name: "打电话聊天",
          isdone: false,
        },
        {
          count: "10",
          name: "五禽戏",
          isdone: false,
        },
        {
          count: "10",
          name: "憋气",
          isdone: false,
        },
        {
          count: "30",
          name: "走亲戚",
          isdone: false,
        },
        {
          count: "40",
          name: "打够三个人",
          isdone: false,
        },
        {
          count: "5",
          name: "20个俯卧撑",
          isdone: false,
        },
        {
          count: "25",
          name: "一套组合拳",
          isdone: false,
        },
      ],
      percentage: 20,
      customColors: [
        {color: '#f56c6c', percentage: 20},
        {color: '#e6a23c', percentage: 40},
        {color: '#5cb87a', percentage: 60},
        {color: '#1989fa', percentage: 80},
        {color: '#6f7ad3', percentage: 100}
      ],
      //奖励
      award: [
        {
          name: "刷剧",
          count: "30",
          img:'30.png',
          istacke:false,
          issure:false,
        },
        {
          name: "加菜",
          count: "60",
          img:'60.png',
          istacke:false,
          issure:false,
        },
        {
          name: "打游戏",
          count: "90",
          img:'90.png',
          istacke:false,
          issure:false,
        },
        {
          name: "买零食水果",
          count: "120",
          img:'120.png',
          istacke:false,
          issure:false,
        },
        {
          name: "睡个好觉",
          count: "150",
          img:'150.png',
          istacke:false,
          issure:false,
        },
        {
          name: "找大佬玩",
          count: "500",
          img:'500.png',
          istacke:false,
          issure:false,
        },
        {
          name: "离职",
          count: "1000",
          img:'1000.png',
          istacke:false,
          issure:false,
        },
      ],
      //得分记录
      histry: [],
      sureshow:false,
      //单个领取
      receive(item) {
        //获取下标
        let index = gamelist.list.findIndex(
          (element) => element.name == item.name
        );
        //根据下标修改状态
        gamelist.list[index].isdone = !item.isdone;
        //推入今天历史记录
        gamelist.today.day = gamelist.getDateTime()
        gamelist.today.todaylist.unshift(item)
        setting.addhistry()
        //存入缓存中
        console.log('存入缓存中',gamelist)
        localStorage.removeItem('gamelist')
        let need = updateNum(gamelist)
        localStorage.setItem("gamelist", JSON.stringify(need));
        gold.sureshow = true
        gold.count = item.count
      },
      
      //获取当前日期
      getDateTime() {
        // 获取当前日期
        let timestamp = Date.parse(new Date());
        let date = new Date(timestamp);
        date.setDate(date.getDate());
        //获取年份
        var Y = date.getFullYear();
        //获取月份
        var M =
          date.getMonth() + 1 < 10
            ? "0" + (date.getMonth() + 1)
            : date.getMonth() + 1;
        //获取当日日期
        var D = date.getDate() < 10 ? "0" + date.getDate() : date.getDate();
        let todayDate = Y + "-" + M + "-" + D;
        return todayDate;
      },
    })
    let gold = reactive({
      sureshow:false,
      //弹窗分数
      count:5,
      img:'',
      awardTip:'',
      getAssetsFile : (url) => {
        const path = require(`../assets/${url}`);
        return path
      },
      //奖励是否高亮
      isactive(a,b) {
        let test = false
        if(a&&b){
          test = false
        }else if(a&&!b){
          test = true
        }else{
          test = false
        }
        return test
      },
      //关闭弹窗
      surecolose(){
        gold.sureshow = false
      },
      tackaward(index,active){
        if(gamelist.award[index].istacke == false&&active){
          gold.sureshow = true
          gold.count = gamelist.award[index].count
          gold.img = 'human.png'
          let awardTip = gamelist.award[index].name+' '
          gold.awardTip = `请勿不辜负每一分努力，${awardTip.repeat(3)}吧！`
          // gamelist.award[index].istacke = true
          //存入缓存中
          let need = updateNum(gamelist)
          localStorage.setItem("gamelist", JSON.stringify(need));
        }
      },
      //计算当天得分百分百
      getpercentage(item){
        gamelist.daycount =item
        gamelist.percentage = item/150*100
      }
    })
    let setting = reactive({
      //自定义游戏规则
      dialogFormVisible:false,
      showViewer:false,
      checkBgBox:false,
      formLabelWidth : '140px',
      imgArray : 14,
      index : 0,
      srcList:[],
      setSrc:() => {
        console.log('setting.imgArray',setting.imgArray)
        for (let index = 0; index < setting.imgArray; index++) {
          const element = gold.getAssetsFile(`bg/${index}.jpg`);
          setting.srcList.push(element)
        }
      },
      setAward:(item) => {
        gamelist.weekcount =item
        if(gamelist.weekcount>=1000){
          gamelist.award[6].issure = true
        }
        if(gamelist.weekcount>=500){
          gamelist.award[5].issure = true
        }
      },
      pick:(index) => {
        if(index>-1){
          gamelist.index = index
        }
        gamelist.bg = `bg/${gamelist.index}.jpg`
        //存入缓存中
        let need = updateNum(gamelist)
        localStorage.setItem("gamelist", JSON.stringify(need));
        setting.closeViewer()
      },
      switchViewer:(i) => {
        gamelist.index = i
      },
      getShow:() => {
        setting.showViewer = true
      },
      closeViewer:() => {
        setting.showViewer = false
      },
      gotohis:() => {
        router.push('/histry')
      },
      goback:() => {
        router.push('/home')
      },
      pickbg:() => {
        setting.checkBgBox = true
      },
      contrw(){
        gamelist.list.splice(gamelist.list.length-1,1)
      },
      addrw(){
        gamelist.list.push({
          name:'',
          count:'',
          isdone:true
        })
      },
      //推进历史
      addhistry(){
        let today = gamelist.today.day
        console.log('gamelist.histry',gamelist.histry)
        const isLargeNumber = gamelist.histry.findIndex((element) => element.day == today);
        if(isLargeNumber>=0){
          gamelist.histry.splice(isLargeNumber, 1, gamelist.today);
        }else{
          gamelist.histry.push(gamelist.today)
        }
        console.log('isLargeNumber',isLargeNumber,gamelist.histry)
      },
      resetting(){
        gamelist.histry = gamelist.histry.filter(item =>item.day!==gamelist.getDateTime())
        gamelist.today = {
          day:'',
          todaylist:[]
        }
        gamelist.list.map((item)=>{
          item.isdone =false
        })
        gamelist.award.map((item)=>{
          item.issure =false
          item.istacke =false
        })
      },
      suregameon(){
        setting.resetting()
        //存入缓存中
        let need = updateNum(gamelist)
        localStorage.setItem("gamelist", JSON.stringify(need));
        setting.dialogFormVisible = false
      },
      //获取当前周几
      getWeekDate() {
        // 获取当前日期
        let timestamp = Date.parse(new Date());
        let now = new Date(timestamp);
        let day = now.getDay();
        let weeks = new Array(
          "周日",
          "周一",
          "周二",
          "周三",
          "周四",
          "周五",
          "周六"
        );
        let week = weeks[day];
        return week;
      },
      //数据回显
      showgame() {
        //读取本地数据
        let oldgamelist = localStorage.getItem("gamelist");
        //判断本地数据是否存在
        if (oldgamelist) {
          let old = JSON.parse(oldgamelist);
          // console.log('数据回显qiang',oldgamelist,old,old.list)
          //数据回显
          gamelist.award = old.award
          gamelist.bg = old.bg
          gamelist.index = old.index
          gamelist.list = old.list
          gamelist.today = old.today
          gamelist.histry = old.histry||[]
          console.log('数据回显',gamelist)
          //历史数据中是否包含今天
          //返回符合条件的下标
          const ishas = gamelist.histry.findIndex((element) => element.day == gamelist.getDateTime());
          console.log('666',ishas)
          //包含
          if(ishas>-1){
            console.log('666')
          }
          // 不包含
          else{
            setting.resetting()
            let test = setting.getWeekDate()
            if(test=='周一'){
              gamelist.histry =[]
            }
          }
          
        }
      },
    })
    const updateNum = (val) => {
      let {award, bg, list,today,histry,index} = val;
      let item={
        award, bg, list,today,histry,index
      }
      return item
    };
    // 通过computed获得doubleCount
    let fixlist = computed(() => {
      let test = [...gamelist.list].sort(function (a, b) {
          return a.isdone - b.isdone;
        });
        // console.log('test',test)
      return test
    })
    // 计算本周得分
    let weekcount = computed(() => {
      let weekcount = 0
      if(gamelist.histry.length>1){
        gamelist.histry.forEach(event=>{
          if(event.todaylist.length>1){
              event.todaylist.forEach(e=>{
                weekcount = weekcount + Number(e.count)
              })
          }else if(event.todaylist.length>0){
              weekcount = event.todaylist[0].count
          }
        })
      }else if(gamelist.histry.length>0){
        weekcount = gamelist.daycount
      }
      setting.setAward(weekcount)
      
      return weekcount
    })
    // 通过computed获得doubleCount
    //计算当天得分
    let daycounts = computed(() => {
          let daycount = 0
          if(gamelist.today.todaylist.length>1){
              gamelist.today.todaylist.forEach(e=>{
                daycount = daycount + Number(e.count)
              })
          }else if(gamelist.today.todaylist.length>0){
              daycount = gamelist.today.todaylist[0].count
          }
          //计算当天得分百分百
          gold.getpercentage(daycount)
          
          return daycount
      })
    // 监听count
    watch(
      () => gamelist.daycount,
      (newVal, oldVal) => {
        if(newVal>=150){
          gamelist.award[4].issure = true
        }
        if(120<=newVal){
          gamelist.award[3].issure = true
        }
        if(90<=newVal){
          gamelist.award[2].issure = true
        }
        if(60<=newVal){
          gamelist.award[1].issure = true
        }
        if(30<=newVal){
          gamelist.award[0].issure = true
        }
        // console.log(gamelist.daycount)
        console.log(`watch监听变化前的数据：${oldVal}`)
        // console.log(`watch监听变化后的数据：${newVal}`)
      },
      {
        immediate: true, // 立即执行
        deep: true // 深度监听
      }
    )
    //数据回显
    setting.showgame();
    setting.setSrc();
</script>
<style lang="scss">
.diybox{
  height: 94vh;
  overflow-y: auto;
  margin-top: 3vh;
  .el-dialog__body{
    padding-top: 10px;
    padding-bottom:10px;
  }
}
.imgdiy{
  height: auto;
}
.el-progress__text{
  color: #fff;
}
@media screen and (max-width: 600px) {
    .diybox{
      width: 80vw;
    }
}

</style>
<style scoped lang="scss">
.el-image-viewer__select{
  width: 44px;
  height: 44px;
  font-size: 24px;
  color: #fff;
  background-color: var(--el-text-color-regular);
  border-color: #fff;
  right: auto;
  left: 40px;
  top: 40px;
}

.imgList{
  display: flex;
  flex-wrap: wrap;
  .imgboss{
    position: relative;
  }
  .iconbox{
    width: 33px;
    height: 33px;
    position: absolute;
    right: 10px;
    top: 10px;
    .el-image-viewer__select{
      width: 33px;
      height: 33px;
      left: auto;
      top: 0;
      right: 0;
    }
    .bggreen{
      background: #00cc6a;
    }
  }
  .imgboxdiy{
    width: 100px;
    height: 100px;
    padding: 5px;
    border-radius: 20px;
    overflow: hidden;
  }
