  cursor: pointer;
}
@-webkit-keyframes buzz {
  0% {
    transform: translateX(-5px) rotate(-10deg); /*设置只在Z轴上移动*/
  }
  50% {
    transform: translateX(5px) rotate(10deg); /*设置只在Z轴上移动*/
  }
  100%{
    transform: translateX(-5px) rotate(-10deg);
  }
}

